@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500&;display=swap);
body {
  margin: 0;
  background-color: #040714;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
    font-family: inter;
    font-weight: "300";
    -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
}
.App {
  overflow-x: hidden;
}

.about {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  height: calc(100vh - 100px);
  padding-bottom: 20px;
}
.about_heading {
  flex: 0.2 1;
  width: 100vw;
  padding-left: 100px;
}
.about_heading h1 {
  padding-top: 25px;
  font-size: 50px;
}

.about_heading span {
  color: #64ffda;
}
.about_content {
  flex: 0.8 1;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-top: 65px;
}
.about_content_left {
  flex: 0.6 1;
  height: 100%;
  padding-left: 100px;
}
.about_content_left h4 {
  font-size: 20px;
  font-weight: normal;
  text-align: left;
}
.about_content_right {
  flex: 0.4 1;
  display: flex;
  justify-content: center;
  height: 100%;
}
.about_content_right_inner_block {
  padding: 5px;
  background: #64ffda;
  -webkit-transform: rotate(45deg) translateY(10px);
          transform: rotate(45deg) translateY(10px);
  width: 250px;
  height: 250px;
}
.losange,
.losange {
  margin: 0 auto;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  overflow: hidden;
  width: 250px;
  height: 250px;
}

.losange .los1 {
  width: 355px;
  height: 355px;
  -webkit-transform: rotate(-45deg) translateY(-74px);
          transform: rotate(-45deg) translateY(-74px);
  box-shadow: 5px red;
}
.losange .los1 img {
  width: 100%;
  height: auto;
}
@media only screen and (max-width: 768px) {
  .about_heading {
    padding-left: 20px;
  }
  .about_heading h1{
    font-size:40px;
  }
  .about_content{
    padding-top:0px;
  }
  .about_content_right {
    display: none;
  }
  .about_content_left {
    flex:1 1;
    padding:0px 20px;
    word-wrap: break-word;
  }
}

