.nav {
  position: fixed;
  top: 0;
  height: 80px;
  width: 100vw;
  background-color: #0c0c1d;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid black;
}
.top_layer {
